import React, { Component } from "react";
import axios from "axios";
import Show from "./showres";
export default class responses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allforms: [],
      selectedform: {},
    };
    this.selected = this.selected.bind(this);
  }

  componentWillMount() {
    axios
      .get("https://formbuilder-umid.herokuapp.com/getallresponses")
      .then((res) => {
        console.log(res.data);
        this.setState({
          allforms: res.data,
        });
      });
    // console.log(this.state);
  }

  showandfill = (e) => {};

  selected = (e, i) => {
    this.setState({
      selectedform: this.state.allforms[i],
    });
    // console.log(this.state);
  };
  render() {
    return (
      <div style={{ margin: "5vh" }}>
        <h2>Please select any one response</h2>
        <div className="myforms">
          {this.state.allforms.map((node, i) => {
            {
              /* console.log(this.state.selectedform); */
            }
            let count = i;
            return (
              <div className="form_conatiner">
                <h3 onClick={(e, count) => this.selected(e, i)}>
                  Response {i + 1}
                </h3>
              </div>
            );
          })}
        </div>
        {this.state.selectedform.hasOwnProperty("panel") ? (
          <div className="added_panels">
            {/* <ShowandFill props={this.state.selectedform} /> */}
            <Show props={this.state.selectedform} />
          </div>
        ) : (
          <p>No forms available</p>
        )}
      </div>
    );
  }
}
