import React, { Component } from "react";
import Dropdown from "react-bootstrap/Dropdown";
import Button from "react-bootstrap/Button";
import { BsFillTrashFill } from "react-icons/bs";
import axios from "axios";
import { Link } from "react-router-dom";
export default class formBuilder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      panels: [],
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleStatic.bind(this);
  }

  handleStatic = (e) => {
    let panel = {
      type: "static",
      value: "",
      edit: true,
    };
    let addedPanel = [...this.state.panels];
    addedPanel.push(panel);
    this.setState({
      panels: addedPanel,
    });
    // console.log(this.state, "handlestatic");
  };

  handleText = (e) => {
    let panel = {
      type: "text",
      value: "",
      label: "",
      edit: true,
    };
    let addedPanel = [...this.state.panels];
    addedPanel.push(panel);
    this.setState({
      panels: addedPanel,
    });
    // console.log(this.state, "handlestatic");
  };

  handleRadio = (e) => {
    let panel = {
      type: "radio",
      values: [""],
      value: "",
      edit: true,
    };
    let addedPanel = [...this.state.panels];
    addedPanel.push(panel);
    this.setState({
      panels: addedPanel,
    });
    // console.log(this.state, "handlestatic");
  };

  handleDrop = (e) => {
    let panel = {
      type: "dropdown",
      values: [""],
      value: "",
      edit: true,
    };
    let addedPanel = [...this.state.panels];
    addedPanel.push(panel);
    this.setState({
      panels: addedPanel,
    });
    // console.log(this.state, "handlestatic");
  };
  changeOptions = (e, i, j) => {
    let node = this.state.panels;
    node[i].values[j] = e.target.value;
    this.setState({
      panels: node,
    });
    // console.log(this.state, e, i, j);
  };

  addOptions = (e, i) => {
    let node = this.state.panels;
    node[i].values.push("");
    this.setState({
      panels: node,
    });
  };

  deleteOptions = (e, i, j) => {
    let node = this.state.panels;
    node[i].values.splice(j, 1);
    // node[i].values.push("");
    this.setState({
      panels: node,
    });
  };

  handleChange = (e, i) => {
    let x = this.state.panels;
    x[i].value = e.target.value;
    this.setState({
      panels: x,
    });
    // console.log(this.state);
  };

  removepanel = (e, i) => {
    let node = this.state.panels;
    node.splice(i, 1);
    this.setState({
      panels: node,
    });
  };

  afterUpload = (e) => {
    const fileReader = new FileReader();
    fileReader.readAsText(e.target.files[0], "UTF-8");
    let mydatas;
    let mypanel = [];
    fileReader.onload = (e) => {
      mydatas = JSON.parse(e.target.result);
      // console.log(
      //   "e.target.result",
      //   JSON.parse(e.target.result),
      //   e.target.result
      // );
      // console.log("uploaded");
      //   setFiles(e.target.result);
      mydatas.map((node) => {
        mypanel.push(node);
      });
      this.setState({
        panels: mypanel,
      });
    };
  };

  downloadFile = async () => {
    let myData = this.state.panels;

    // console.log(myData, "hey");
    const fileName = "file";
    const json = JSON.stringify(myData);
    // console.log(json);
    const blob = new Blob([json], { type: "application/json" });
    const href = await URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.href = href;
    link.download = fileName + ".json";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  publish = (e) => {
    if (this.state.panels.length == 0) alert("Please build your form first.");
    axios
      .post(
        "https://formbuilder-umid.herokuapp.com/saveform",
        this.state.panels
      )
      .then((res) => {
        // console.log(res);
        // console.log(res.data);
      });
  };
  render() {
    return (
      <div>
        <div className="buttons">
          {/* <input type="file">Import JSON</input> */}
          {/* <input type="file" onChange={this.afterUpload} /> */}
          <input
            type="file"
            name="file"
            id="file"
            class="inputfile"
            onChange={this.afterUpload}
            accept="json"
          />
          <label for="file">Upload JSON</label>
          <a onClick={this.downloadFile} style={{ color: "white" }}>
            Download JSON
          </a>
          <Link to="/allforms">All forms</Link>
          <Link to="/responses">Responses</Link>
        </div>

        <div className="added_panels">
          <h2 style={{ marginLeft: "-16vw" }}>Start Building your form.</h2>
          <div>
            {this.state.panels.map((node, i) => {
              let count = i;
              if (node.type == "static")
                return (
                  <div className="added_panel">
                    {node.edit ? (
                      <input
                        type="text"
                        value={node.value}
                        name={i}
                        onChange={(e, count) => this.handleChange(e, i)}
                        onBlur={() => {
                          let temp = this.state.panels;
                          temp[count].edit = false;
                          this.setState({
                            panels: temp,
                          });
                        }}
                      ></input>
                    ) : (
                      <p>
                        {node.value}{" "}
                        <span
                          onClick={() => {
                            let temp = this.state.panels;
                            temp[count].edit = true;
                            this.setState({
                              panels: temp,
                            });
                          }}
                        >
                          Edit
                        </span>
                      </p>
                    )}
                    <div
                      className="remove_button"
                      onClick={(e, count) => this.removepanel(e, i)}
                    >
                      <BsFillTrashFill />
                    </div>
                  </div>
                );
              else if (node.type == "text") {
                return (
                  <div className="added_panel text_panel">
                    {node.edit ? (
                      <input
                        type="text"
                        value={node.value}
                        name={i}
                        onChange={(e, count) => this.handleChange(e, i)}
                        onBlur={() => {
                          let temp = this.state.panels;
                          temp[count].edit = false;
                          this.setState({
                            panels: temp,
                          });
                        }}
                      ></input>
                    ) : (
                      <p>
                        {node.value}
                        <span
                          onClick={() => {
                            let temp = this.state.panels;
                            temp[count].edit = true;
                            this.setState({
                              panels: temp,
                            });
                          }}
                        >
                          Edit
                        </span>
                      </p>
                    )}
                    <input
                      style={{ marginTop: "1vh" }}
                      type="text"
                      value="Answer Here"
                      disabled
                    ></input>
                    <div
                      className="remove_button"
                      onClick={(e, count) => this.removepanel(e, i)}
                    >
                      <BsFillTrashFill />
                    </div>
                  </div>
                );
              } else if (node.type == "radio") {
                return (
                  <div className="added_panel radio_panels">
                    {node.edit ? (
                      <input
                        type="text"
                        value={node.value}
                        name={i}
                        onChange={(e, count) => this.handleChange(e, i)}
                        onBlur={() => {
                          let temp = this.state.panels;
                          temp[count].edit = false;
                          this.setState({
                            panels: temp,
                          });
                        }}
                      ></input>
                    ) : (
                      <p>
                        {node.value}
                        <span
                          onClick={() => {
                            let temp = this.state.panels;
                            temp[count].edit = true;
                            this.setState({
                              panels: temp,
                            });
                          }}
                        >
                          Edit
                        </span>
                      </p>
                    )}
                    <div className="radio_options">
                      {node.values.map((node, j) => {
                        let newcount = j;
                        return (
                          <div>
                            <span>ðŸ”˜</span>
                            <div>
                              <input
                                type="text"
                                value={node}
                                onChange={(e, i, j) => {
                                  {
                                    /* console.log(e, i, j); */
                                  }
                                  this.changeOptions(e, count, newcount);
                                }}
                              ></input>
                            </div>
                            <div
                              className="radio_add"
                              onClick={(e, count) => {
                                this.addOptions(e, i);
                              }}
                            >
                              +
                            </div>
                            <div
                              className="radio_add"
                              onClick={(e, count, newcount) => {
                                this.deleteOptions(e, i, j);
                              }}
                            >
                              {j > 0 ? "-" : ""}
                            </div>
                          </div>
                        );
                      })}
                    </div>
                    <div
                      className="remove_button"
                      onClick={(e, count) => this.removepanel(e, i)}
                    >
                      <BsFillTrashFill />
                    </div>
                  </div>
                );
              } else if (node.type == "dropdown") {
                return (
                  <div className="added_panel radio_panels">
                    {node.edit ? (
                      <input
                        type="text"
                        value={node.value}
                        name={i}
                        onChange={(e, count) => this.handleChange(e, i)}
                        onBlur={() => {
                          let temp = this.state.panels;
                          temp[count].edit = false;
                          this.setState({
                            panels: temp,
                          });
                          {
                            /* console.log(this.state); */
                          }
                        }}
                      ></input>
                    ) : (
                      <p>
                        {node.value}
                        <span
                          onClick={() => {
                            let temp = this.state.panels;
                            temp[count].edit = true;
                            this.setState({
                              panels: temp,
                            });
                          }}
                        >
                          Edit
                        </span>
                      </p>
                    )}
                    <div className="radio_options">
                      {node.values.map((node, j) => {
                        let newcount = j;
                        return (
                          <div>
                            <span style={{ marginRight: "5px" }}>{j + 1}.</span>
                            <div>
                              <input
                                type="text"
                                value={node}
                                onChange={(e, i, j) => {
                                  {
                                    /* console.log(e, i, j); */
                                  }
                                  this.changeOptions(e, count, newcount);
                                  {
                                    /* console.log(this.state); */
                                  }
                                }}
                              ></input>
                            </div>
                            <div
                              className="radio_add"
                              onClick={(e, count) => {
                                this.addOptions(e, i);
                              }}
                            >
                              +
                            </div>
                            <div
                              className="radio_add"
                              onClick={(e, count, newcount) => {
                                this.deleteOptions(e, i, j);
                              }}
                            >
                              {j > 0 ? "-" : ""}
                            </div>
                          </div>
                        );
                      })}
                    </div>
                    <div
                      className="remove_button"
                      onClick={(e, count) => this.removepanel(e, i)}
                    >
                      <BsFillTrashFill />
                    </div>
                  </div>
                );
              }
            })}
          </div>
          <div className="add_button">
            <Dropdown>
              <Dropdown.Toggle variant="success" id="dropdown-basic">
                Add
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item onClick={this.handleStatic}>
                  Static
                </Dropdown.Item>
                <Dropdown.Item onClick={this.handleText}>Text</Dropdown.Item>
                <Dropdown.Item onClick={this.handleRadio}>
                  Radio Buttons
                </Dropdown.Item>
                <Dropdown.Item onClick={this.handleDrop}>
                  Drop Down
                </Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <div className="publish_button">
              <Button onClick={this.publish}>Publish</Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
