import React, { Component } from "react";

export default class showres extends Component {
  constructor(props) {
    super(props);
    // console.log(props, "showres");
  }

  componentWillReceiveProps(props) {
    this.props = props;
  }

  render() {
    return (
      <div className="added_panel">
        {this.props.props.panel.map((node, i) => {
          let count = i;
          if (node.type == "static") {
            return <h3>{node.value}</h3>;
          } else if (node.type == "text") {
            return (
              <div>
                <h3>{node.value}</h3>
                <p>Answer: {node.answer}</p>
              </div>
            );
          } else if (node.type == "radio") {
            return (
              <div>
                <h3>{node.value}</h3>
                <p>Answer: {node.answer}</p>
              </div>
            );
          } else if (node.type == "dropdown") {
            return (
              <div>
                <h3>{node.value}</h3>
                <p>Answer: {node.answer}</p>
              </div>
            );
          }
        })}
      </div>
    );
  }
}
