import React, { Component } from "react";
import axios from "axios";
import Button from "react-bootstrap/Button";
export default class showandfill extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datas: props.props.panel,
      newForm: {},
      newformdata: [],
      panels: [],
    };
  }

  componentWillReceiveProps(props) {
    this.setState({
      datas: props.props.panel,
    });
  }

  componentWillMount() {
    // console.log(this.state.datas, "wee");
  }

  textChange = (e, i) => {
    let x = this.state.datas;
    let answer = e.target.value;
    x[i].answer = answer;
    this.setState({
      panels: x,
    });
    // console.log(this.state);
  };

  sendresponse = (e) => {
    axios
      .post(
        "https://formbuilder-umid.herokuapp.com/saveresponse",
        this.state.panels
      )
      .then((res) => {
        // console.log(res);
        // console.log(res.data);
      });
  };
  render() {
    return (
      <div className="added_panels">
        <div className="added_panel">
          {this.state.datas.map((node, i) => {
            let count = i;
            if (node.type == "static") {
              return <h3>{node.value}</h3>;
            } else if (node.type == "text") {
              return (
                <div>
                  <h3>{node.value}</h3>
                  <input
                    type="text"
                    onChange={(e, count) => this.textChange(e, i)}
                    placeholder="Answer here"
                  ></input>
                </div>
              );
            } else if (node.type == "radio") {
              return (
                <div>
                  <h3>{node.value}</h3>
                  {node.values.map((options) => {
                    return (
                      <div
                        onChange={(e, count) => {
                          this.textChange(e, i);
                        }}
                        className="radio_distance"
                      >
                        <input type="radio" value={options} name={count} />
                        {options}
                      </div>
                    );
                  })}
                </div>
              );
            } else if (node.type == "dropdown") {
              return (
                <div className="drop_distance">
                  <h3>{node.value}</h3>
                  <select
                    onChange={(e, count) => {
                      this.textChange(e, i);
                      {
                        /* console.log(e.target.value); */
                      }
                    }}
                  >
                    {node.values.map((options) => {
                      return <option value={options}>{options}</option>;
                    })}
                  </select>
                </div>
              );
            }
          })}
        </div>
        <Button style={{ marginTop: "2vh" }} onClick={this.sendresponse}>
          Send response
        </Button>
      </div>
    );
  }
}
