import React, { Component } from "react";
import axios from "axios";
import ShowandFill from "./showandfill";
export default class allforms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allforms: [],
      selectedform: {},
    };
    this.selected = this.selected.bind(this);
  }

  componentWillMount() {
    axios.get("https://formbuilder-umid.herokuapp.com/getall").then((res) => {
      // console.log(res.data);
      this.setState({
        allforms: res.data,
      });
    });
    // console.log(this.state);
  }

  showandfill = (e) => {};

  selected = (e, i) => {
    this.setState({
      selectedform: [],
    });
    this.setState({
      selectedform: this.state.allforms[i],
    });
    // console.log(this.state);
  };
  render() {
    return (
      <div>
        <div style={{ margin: "5vh" }}>
          <h2>Please select any one form</h2>
          <div className="myforms">
            {this.state.allforms.map((node, i) => {
              {
                /* console.log(this.state.selectedform); */
              }
              let count = i;
              return (
                <div
                  className="form_conatiner"
                  onClick={(e, count) => this.selected(e, i)}
                >
                  <h1>Form {i + 1}</h1>
                </div>
              );
            })}
          </div>
        </div>
        {this.state.selectedform.hasOwnProperty("panel") ? (
          <div className="added_panels">
            <ShowandFill props={this.state.selectedform} />
          </div>
        ) : (
          <p>No forms Selected</p>
        )}
      </div>
    );
  }
}
